body {
  background-color: #779cd4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #333;
  padding: 1rem;
}

.search-form {
  justify-content: center;
  align-items: center;
  margin: 1rem auto;
  display: flex;
}

.search-form input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 5px 0 0 5px;
  padding: .5rem;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #19d6cd;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: .5rem 1rem;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  padding: 1rem;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 1rem;
  transition: transform .2s;
  box-shadow: 0 0 5px rgba(0, 0, 0, .2);
}

.photo-card img {
  max-width: 100%;
  height: auto;
}

.info {
  margin-top: 1rem;
}

.info-item {
  margin: .5rem 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 5px;
  margin: 1rem auto;
  padding: .5rem 1rem;
  display: block;
}

